@import url("https://fonts.googleapis.com/css2?family=Fraunces:wght@500;600;700&family=Manrope:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

:root {
  font-family:
    system-ui,
    -apple-system,
    Segoe UI,
    Roboto,
    Arial,
    sans-serif;
}
html {
  overflow-y: scroll;
  scrollbar-gutter: stable;
  height: 100%;
}
body {
  margin: 0;
  background: #f6f7fb;
  color: #111;
  min-height: 100%;
}
a {
  color: #2563eb;
  text-decoration: none;
}
.container {
  max-width: 100%;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
}
.container.school-page {
  max-width: 100%;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 96px;
}
.card {
  max-width: 100%;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 14px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
}
.row {
  display: flex;
  gap: 12px;
  align-items: center;
  flex-wrap: wrap;
}
.btn {
  border: 1px solid #d1d5db;
  background: #fff;
  padding: 8px 12px;
  border-radius: 10px;
  cursor: pointer;
  transition:
    background 0.15s ease,
    border-color 0.15s ease,
    transform 0.15s ease,
    box-shadow 0.15s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.btn:hover {
  background: #f3f4f6;
  border-color: #cbd5e1;
}
.btn.primary {
  background: #111827;
  color: white;
  border-color: #111827;
}
.btn.primary:hover {
  background: #0f172a;
  border-color: #0f172a;
}
.btn.success {
  background: #16a34a;
  color: #fff;
  border-color: #16a34a;
}
.btn.success:hover {
  background: #15803d;
  border-color: #15803d;
}
.btn.danger {
  background: #dc2626;
  color: white;
  border-color: #dc2626;
}
.btn.danger:hover {
  background: #b91c1c;
  border-color: #b91c1c;
}
.btn.full {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.btn-primary {
  background: #111827;
  color: #fff;
  border-color: #111827;
}
.btn-primary:hover {
  background: #0f172a;
  border-color: #0f172a;
}
.btn-ghost {
  background: rgba(15, 23, 42, 0.03);
  border-color: rgba(226, 232, 240, 0.9);
  color: inherit;
}
.btn-danger {
  background: #dc2626;
  color: #fff;
  border-color: #dc2626;
}
.btn-danger:hover {
  background: #b91c1c;
  border-color: #b91c1c;
}
.btn-sm {
  height: 32px;
  padding: 0 10px;
  border-radius: 999px;
  font-size: 12px;
}
.btn-md {
  height: 36px;
  padding: 0 12px;
  border-radius: 999px;
  font-size: 13px;
  font-weight: 800;
}
.btn-lg {
  height: 40px;
  padding: 0 14px;
  border-radius: 999px;
  font-size: 14px;
  font-weight: 800;
}
.btn:disabled {
  opacity: 0.55;
  cursor: not-allowed;
}

/* Unified header / navigation buttons (same size for <a> and <button>) */
/*
 * Unified header / navigation buttons for the topbar. These buttons adopt a
 * minimalistic style inspired by the provided example: a subtle border,
 * gentle corner radius, and light shadow. They are designed as inline-flex
 * containers so that icons and labels can be placed side‑by‑side with
 * consistent spacing. The heavy transforms on hover/active have been
 * removed in favour of a simple background change.
 */
.nav-btn {
  height: 36px;
  padding: 8px 10px;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  background: #fff;
  font-weight: 600;
  font-size: 13px;
  cursor: pointer;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  transition: background 160ms ease, opacity 160ms ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  user-select: none;
  text-decoration: none;
  color: #111;
}
.nav-btn:hover {
  background: #f8fafc;
}
.nav-btn:active {
  /* On active, slightly reduce the opacity to mimic the example’s pressed state. */
  opacity: 0.92;
}
.nav-btn:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  box-shadow: none;
}

/* Adjust the size and colour of icons inside nav buttons. Icons should be
   slightly larger than the surrounding text and the chevron should appear
   muted to match the example design. */
.nav-btn svg {
  font-size: 16px;
}
.nav-btn svg:last-child {
  color: #666;
}
.nav-btn.is-ghost {
  background: rgba(15, 23, 42, 0.03);
}
.nav-btn.is-save {
  border-color: rgba(245, 158, 11, 0.55);
  background: #fffbeb;
}
.nav-btn.is-primary {
  background: #111827;
  border-color: #111827;
  color: #fff;
  box-shadow: 0 12px 26px rgba(15, 23, 42, 0.18);
}
.nav-btn.is-primary:hover {
  background: #0f172a;
  border-color: #0f172a;
}
.nav-btn.is-danger {
  background: #dc2626;
  border-color: #dc2626;
  color: #fff;
  box-shadow: 0 12px 26px rgba(220, 38, 38, 0.18);
}
.nav-btn.is-danger:hover {
  background: #b91c1c;
  border-color: #b91c1c;
}

.input {
  border: 1px solid #d1d5db;
  border-radius: 10px;
  padding: 8px 10px;
  width: 260px;
}
.input.sm {
  width: 140px;
}
.input.xs {
  width: 110px;
  padding: 6px 8px;
  border-radius: 10px;
}
.input.xxs {
  width: 92px;
  padding: 6px 8px;
  border-radius: 10px;
}
.input[disabled] {
  background: #f9fafb;
  color: #6b7280;
}
.input-dirty {
  border-color: #f59e0b;
  background: #fffbeb;
  box-shadow: 0 0 0 2px rgba(245, 158, 11, 0.15);
}
input[data-decimal-separator],
input[type="number"],
.input.num {
  text-align: right;
  font-variant-numeric: tabular-nums;
}
input[type="number"] {
  appearance: textfield;
  -moz-appearance: textfield;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  border-bottom: 1px solid #eef2f7;
  padding: 8px;
  text-align: left;
}
.table th {
  background: #f9fafb;
  font-weight: 600;
}
.table td.num,
.table th.num {
  text-align: right;
  white-space: nowrap;
}
.table td.nowrap {
  white-space: nowrap;
}
.table thead tr.group th {
  background: #f3f4f6;
  font-weight: 800;
  font-size: 12px;
}
.table tbody tr:hover td {
  background: rgba(17, 24, 39, 0.02);
}
.table td {
  vertical-align: middle;
}
.perm-mark {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 26px;
  height: 26px;
  border-radius: 999px;
  border: 1px solid transparent;
}
.perm-mark.is-yes {
  background: #ecfdf3;
  border-color: #bbf7d0;
  color: #16a34a;
}
.perm-mark.is-no {
  background: #fef2f2;
  border-color: #fecaca;
  color: #ef4444;
}
.perm-mark-icon {
  width: 14px;
  height: 14px;
  stroke: currentColor;
  stroke-width: 2.5;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
}
.perm-toggle {
  border: 0;
  background: transparent;
  padding: 0;
  cursor: pointer;
}
.perm-toggle .perm-mark {
  transition: transform 120ms ease, box-shadow 120ms ease;
}
.perm-toggle:hover .perm-mark {
  transform: translateY(-1px);
  box-shadow: 0 6px 12px rgba(15, 23, 42, 0.12);
}
.permissions-table th,
.permissions-table td {
  padding: 4px 6px;
  line-height: 1.1;
  font-size: 12px;
}
.permissions-table .perm-mark {
  width: 22px;
  height: 22px;
}
.permissions-table .perm-mark-icon {
  width: 12px;
  height: 12px;
}
.permissions-table .input.sm {
  height: 28px;
  padding: 4px 8px;
  font-size: 12px;
}
.permissions-table {
  table-layout: fixed;
}
.permissions-table th:first-child,
.permissions-table td:first-child {
  width: 55%;
}
.permissions-table th:nth-child(2),
.permissions-table td:nth-child(2),
.permissions-table th:nth-child(3),
.permissions-table td:nth-child(3) {
  width: 80px;
}
.permissions-table th:nth-child(4),
.permissions-table td:nth-child(4) {
  width: 200px;
}
.permissions-table td:nth-child(4) .input.sm {
  width: 100%;
}
.permissions-table th:nth-child(2),
.permissions-table th:nth-child(3),
.permissions-table td:nth-child(2),
.permissions-table td:nth-child(3) {
  text-align: center;
}

/* Select page fixed table panels */
.select-page-tables {
  --select-table-height: clamp(320px, 60vh, 640px);
}

/* ==== Access Management hub styles ====
 * These classes are used for the refactored ManagePermissionsPage,
 * which now acts as a hub for managing users, permissions, schools,
 * and principal assignments. The design follows the existing design
 * language with subtle borders, rounded corners and muted colours.
 */

/* Tab bar at the top of the hub content */
.access-hub-tabs {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
  margin-bottom: 16px;
  gap: 24px;
}
.access-hub-tab {
  padding: 8px 0;
  font-weight: 600;
  font-size: 14px;
  color: #6b7280;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: color 120ms ease, border-color 120ms ease;
}
.access-hub-tab:hover {
  color: #374151;
}
.access-hub-tab.is-active {
  color: #111827;
  border-color: #111827;
}

/* Two‑pane grid used by both tabs */
.access-hub-grid {
  display: flex;
  gap: 24px;
  align-items: flex-start;
}
.access-hub-grid .left-pane {
  width: 280px;
  flex-shrink: 0;
}
.access-hub-grid .right-pane {
  flex: 1;
  min-width: 0;
}
@media (max-width: 768px) {
  .access-hub-grid {
    flex-direction: column;
  }
  .access-hub-grid .left-pane,
  .access-hub-grid .right-pane {
    width: 100%;
  }
}

/* List item for users and schools */
.access-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  background: #fff;
  cursor: pointer;
  transition: background 120ms ease, border-color 120ms ease;
  margin-bottom: 8px;
}
.access-list-item:hover {
  background: #f9fafb;
}
.access-list-item.is-selected {
  background: #eff6ff;
  border-color: #bfdbfe;
}
.role-tag {
  background: #f3f4f6;
  color: #374151;
  border: 1px solid #e5e7eb;
  border-radius: 999px;
  padding: 2px 8px;
  font-size: 12px;
  display: inline-flex;
  align-items: center;
  gap: 4px;
}
.role-tag.is-ok {
  background: rgba(16, 185, 129, 0.12);
  border-color: rgba(16, 185, 129, 0.45);
  color: #065f46;
}
.role-tag.is-warn {
  background: rgba(245, 158, 11, 0.12);
  border-color: rgba(245, 158, 11, 0.5);
  color: #92400e;
}
.role-tag.is-muted {
  background: rgba(148, 163, 184, 0.18);
  border-color: rgba(148, 163, 184, 0.4);
  color: #475569;
}

/* ==== School assignments page ==== */
.school-assignments-page {
  padding: 24px 0 32px;
}
.school-assignments-header h2 {
  margin: 0 0 4px;
  font-size: 24px;
}
.school-assignments-header p {
  margin: 0;
  color: #6b7280;
}
.school-assignments-table-wrap {
  margin-top: 16px;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  background: #ffffff;
  overflow: hidden;
}
.school-assignments-table {
  width: 100%;
  font-size: 13px;
}
.school-assignments-row {
  cursor: pointer;
  transition: background 0.2s ease;
}
.school-assignments-table tbody tr.school-assignments-row:hover td {
  background: #f9fafb;
}
.school-assignments-name {
  font-weight: 600;
}
.school-assignments-missing {
  color: #b45309;
}
.school-assignments-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.35);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease;
  z-index: 1000;
}
.school-assignments-overlay.is-open {
  opacity: 1;
  pointer-events: auto;
}
.school-assignments-drawer {
  position: fixed;
  top: 0;
  right: -420px;
  width: min(420px, 92vw);
  height: 100%;
  background: #ffffff;
  border-left: 1px solid #e5e7eb;
  box-shadow: -12px 0 32px rgba(15, 23, 42, 0.2);
  transition: right 0.3s ease;
  z-index: 1010;
  display: flex;
  flex-direction: column;
}
.school-assignments-drawer.is-open {
  right: 0;
}
.school-assignments-drawer-header {
  padding: 16px 20px;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.school-assignments-drawer-title {
  font-weight: 700;
}
.school-assignments-drawer-subtitle {
  color: #6b7280;
  font-size: 13px;
}
.school-assignments-close {
  width: 32px;
  height: 32px;
  padding: 0;
  border-radius: 8px;
  font-size: 18px;
  line-height: 1;
}
.school-assignments-drawer-body {
  padding: 16px 20px 24px;
  overflow-y: auto;
  flex: 1;
}
.school-assignments-section-title {
  font-size: 12px;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  margin: 16px 0 8px;
}
.school-assignments-card {
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 12px;
  background: #f9fafb;
  margin-bottom: 12px;
}
.school-assignments-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}
.school-assignments-card-name {
  font-weight: 600;
}
.school-assignments-card-role {
  color: #6b7280;
  font-size: 12px;
}
.school-assignments-modules {
  margin-top: 10px;
  display: grid;
  gap: 6px;
}
.school-assignments-module-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
}
.school-assignments-add-card {
  border: 1px dashed #d1d5db;
  padding: 12px;
  border-radius: 10px;
  background: #ffffff;
}
.school-assignments-add-row {
  display: grid;
  grid-template-columns: 1fr 120px auto;
  gap: 8px;
  align-items: center;
}
.school-assignments-add-row .input {
  width: 100%;
}
.school-assignments-missing-line {
  margin-top: 16px;
  padding-top: 12px;
  border-top: 1px solid #e5e7eb;
  font-size: 13px;
  display: flex;
  align-items: center;
  gap: 6px;
}
.school-assignments-missing-label {
  font-weight: 600;
}
.school-assignments-empty {
  padding: 12px 0;
  color: #6b7280;
}
@media (max-width: 720px) {
  .school-assignments-table th:nth-child(2),
  .school-assignments-table td:nth-child(2),
  .school-assignments-table th:nth-child(3),
  .school-assignments-table td:nth-child(3),
  .school-assignments-table th:nth-child(4),
  .school-assignments-table td:nth-child(4) {
    display: none;
  }
  .school-assignments-add-row {
    grid-template-columns: 1fr;
  }
}

/* ==== User management list (ManagePermissionsPage) ==== */
.user-management-section {
  margin-top: 24px;
}
.user-management-title {
  font-weight: 700;
  margin-bottom: 12px;
}
.user-management-list {
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  overflow: hidden;
  background: #fff;
}
.user-management-header,
.user-management-row {
  display: grid;
  grid-template-columns: 1.4fr 1.4fr 0.8fr 1fr;
  gap: 12px;
  align-items: center;
  padding: 10px 12px;
}
.user-management-header {
  background: #f3f4f6;
  font-size: 12px;
  font-weight: 600;
  color: #6b7280;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}
.user-management-row {
  border-top: 1px solid #e5e7eb;
}
.user-management-name {
  display: flex;
  flex-direction: column;
  gap: 4px;
  font-weight: 600;
}
.user-management-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.user-management-role {
  font-weight: 600;
  color: #6b7280;
}
.user-management-temp {
  grid-column: 1 / -1;
  font-size: 12px;
  color: #92400e;
  background: #fffbeb;
  border: 1px solid rgba(245, 158, 11, 0.3);
  padding: 6px 10px;
  border-radius: 8px;
}
.user-management-temp-value {
  font-weight: 700;
}
@media (max-width: 960px) {
  .user-management-header {
    display: none;
  }
  .user-management-row {
    grid-template-columns: 1fr;
  }
}

/* Drawer overlay and panel */
.drawer-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.45);
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  z-index: 1000;
}
.drawer {
  width: min(420px, 100%);
  max-width: 100%;
  background: #fff;
  border-left: 1px solid #e5e7eb;
  box-shadow: -12px 0 28px rgba(0, 0, 0, 0.12);
  padding: 16px;
  overflow-y: auto;
}

/* Chips for assigned principals */
.chip {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  background: #f3f4f6;
  border: 1px solid #e5e7eb;
  border-radius: 999px;
  font-size: 12px;
}
.chip-remove {
  border: none;
  background: transparent;
  color: #b91c1c;
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
}
.chip-remove:hover {
  color: #7f1d1d;
}
.select-table-card {
  display: flex;
  flex-direction: column;
  height: var(--select-table-height);
  min-height: var(--select-table-height);
}
.select-table-body {
  flex: 1 1 auto;
  min-height: 0;
  overflow: auto;
  scrollbar-gutter: stable;
}
.select-table-body-gap {
  padding-top: 10px;
}
.select-table-body .table thead th {
  position: sticky;
  top: 0;
  z-index: 2;
  background: #f9fafb;
}
.select-school-table {
  table-layout: fixed;
}
.select-checkbox {
  width: 16px;
  height: 16px;
  accent-color: #2563eb;
  cursor: pointer;
}
.select-checkbox:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

/* Shared data-table skin */
.table-scroll {
  overflow: auto;
  max-height: 560px;
}
.table-scroll.no-vert-scroll {
  max-height: none;
  overflow-x: auto;
  overflow-y: visible;
}
.data-table {
  width: 100%;
  border-collapse: collapse;
  line-height: 1.2;
}
.data-table th,
.data-table td {
  padding: 6px 8px;
  font-size: 12px;
}
.data-table thead th {
  position: sticky;
  top: 0;
  z-index: 3;
  background: #f9fafb;
  box-shadow:
    inset 0 1px 0 rgba(255, 255, 255, 0.9),
    inset 0 -1px 2px rgba(15, 23, 42, 0.12);
}
.data-table th:first-child,
.data-table td:first-child {
  position: sticky;
  left: 0;
  z-index: 2;
  background: #fff;
  box-shadow: 1px 0 0 rgba(226, 232, 240, 0.9);
}
.data-table thead th:first-child {
  z-index: 4;
  background: #f3f4f6;
}
.data-table tbody tr:nth-child(even) td {
  background: rgba(17, 24, 39, 0.02);
}
.data-table tbody tr:hover td {
  background: rgba(17, 24, 39, 0.05);
}
.cell-num {
  text-align: right;
  font-variant-numeric: tabular-nums;
}
.cell-pct {
  text-align: center;
  font-variant-numeric: tabular-nums;
}
.cell-count {
  text-align: center;
  font-variant-numeric: tabular-nums;
}
.cell-text {
  text-align: left;
}
.row-group-start td {
  border-top: 2px solid rgba(226, 232, 240, 0.9);
}
.data-table td.exp-group-cell {
  background: rgba(13, 148, 136, 0.08);
}

/* Multi-year / divider helpers */
.muted {
  color: #6b7280;
}
.table .sep-left {
  border-left: 2px solid #e5e7eb;
}

/* Expenses grouping */
.exp-group-col {
  width: 30px;
  padding: 0;
}

/* Row-group label column (rotated text, but must NOT affect row heights) */
.exp-group-cell {
  width: 30px;
  min-width: 30px;
  padding: 0;
  text-align: center;
  vertical-align: middle;
  position: relative;
  overflow: visible; /* allow long labels to fit vertically without clipping */
  border-right: 1px solid #e5e7eb;
  background: rgba(13, 148, 136, 0.08);
}

.exp-group-label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-90deg) scale(var(--exp-scale, 1));
  transform-origin: center;
  white-space: nowrap;
  font-weight: 800;
  font-size: 11px;
  letter-spacing: 0.01em;
  color: #0f766e;
  line-height: 1;
  padding: 0;
  z-index: 1;
}

.exp-group-blank {
  width: 30px;
  padding: 0;
  border-right: 1px solid #eef2f7;
}

.table tbody tr.exp-band-edu td {
  background: rgba(13, 148, 136, 0.05);
}
.table tbody tr.exp-band-edu:hover td {
  background: rgba(13, 148, 136, 0.08);
}
/* Expenses: compact + readable */
.expenses-card .table th,
.expenses-card .table td {
  padding: 5px 6px;
  font-size: 11px;
  line-height: 1.15;
}

.expenses-card .table thead th {
  vertical-align: middle;
}
.expenses-card .table thead tr.group th {
  font-size: 11px;
}
.expenses-card .input {
  font-size: 11px;
}
.expenses-card .input.xs,
.expenses-card .input.xxs {
  padding: 4px 6px;
}
.expenses-table-container table.table thead th,
.expenses-table-container table.table thead th.num {
  text-align: center;
}
.expenses-table-container .table-scroll.no-vert-scroll {
  max-height: none;
  overflow-y: visible;
}
.expenses-table-container td.cell-num {
  text-align: right;
}
.expenses-table-container td.cell-pct {
  text-align: center;
}

/* Main (3-year) expenses table */
.expenses-card .expenses-main {
  table-layout: fixed;
  width: 100%;
}
.expenses-card .expenses-main th,
.expenses-card .expenses-main td {
  min-width: 0;
  font-size: clamp(9px, 1vw, 11px);
}
.expenses-card .expenses-main .input.xxs {
  height: 24px;
  width: 100%;
  min-width: 0;
  max-width: 100%;
  font-size: clamp(9px, 1vw, 11px);
  box-sizing: border-box;
}

/* Wider label column so it wraps less */
.expenses-card .expenses-main th.exp-label-col,
.expenses-card .expenses-main td.exp-label-col {
  width: clamp(150px, 28vw, 520px);
  min-width: 150px;
}

.expenses-card .expenses-main td.exp-label-col {
  font-size: clamp(9px, 0.95vw, 10.5px);
  line-height: 1.1;
}

/* Label cell: wrap normally (wider col + smaller font keeps rows compact) */
.expenses-card .expenses-main td.exp-label-col {
  white-space: normal;
  overflow-wrap: anywhere;
}

/* Multi-line header for the % columns (keeps header short without forcing wide columns) */
.expenses-card .exp-th-wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 1px;
  font-size: 10px;
  line-height: 1.05;
  white-space: normal;
}

.expenses-card .exp-col-total {
  width: clamp(55px, 6vw, 130px);
}
.expenses-card .exp-col-pct {
  width: clamp(50px, 5.5vw, 115px);
}
.expenses-card .exp-col-yoy {
  width: clamp(40px, 5vw, 90px);
}

.ik-table-wrap {
  overflow: auto;
  max-height: 520px;
  border-radius: 12px;
}
.ik-table {
  border-collapse: separate;
  border-spacing: 0;
}
.ik-table thead th {
  background: #f3f4f6;
}
.ik-table tbody tr:nth-child(even) td {
  background: #f9fafb;
}
.ik-table tbody tr.ik-total-row td {
  background: #f1f5f9;
  font-weight: 800;
}
.ik-table tbody tr:hover td {
  background: #eef2ff;
}
.ik-table .ik-sticky-col {
  position: sticky;
  left: 0;
  z-index: 1;
  background: #fff;
}
.ik-table thead .ik-sticky-col {
  z-index: 4;
  background: #f3f4f6;
}
.ik-compact-wrap {
  margin-top: 8px;
}
.ik-compact-table {
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
}
.ik-compact-table th,
.ik-compact-table td {
  padding: 4px 6px;
  vertical-align: top;
}
.ik-compact-table thead th {
  background: #f3f4f6;
  vertical-align: middle;
  text-align: center;
}
.ik-compact-table tbody tr:nth-child(even) td {
  background: #f9fafb;
}
.ik-compact-table tbody tr.ik-total-row td {
  background: #f1f5f9;
  font-weight: 700;
}
.ik-compact-level {
  font-size: 12px;
  width: 190px;
  font-weight: 600;
  vertical-align: middle;
  text-align: left;
  border-right: 2px solid #5a5656;
}
.ik-compact-title {
  font-weight: 600;
  font-size: 12px;
}
.ik-compact-group {
  text-align: center;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  padding: 8px 6px;
}
.ik-compact-role {
  text-align: center;
  font-size: 12px;
  line-height: 1.2;
}

.ik-compact-group-dot {
  border-bottom: 1px dotted #775955;
}

.ik-compact-dot {
  border-right: 2px dotted #c4ccd8;
}

.ik-compact-divider {
  border-right: 2px solid #5a5656;
}
.ik-stack {
  display: grid;
  gap: 6px;
}
.ik-stack.ik-stack-condensed {
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 2px;
}
.ik-stack-row {
  display: block;
}
.ik-stack-input {
  width: 100%;
  min-width: 0;
  padding: 4px 6px;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  text-align: right;
  font-size: 11px;
  box-sizing: border-box;
}
.ik-stack-readonly,
.ik-stack-value,
.ik-stack-muted {
  width: 100%;
  min-width: 0;
  padding: 4px 6px;
  border-radius: 8px;
  text-align: right;
  font-size: 11px;
  box-sizing: border-box;
}
.ik-stack-readonly {
  border: 1px dashed #e5e7eb;
  background: #f8fafc;
  color: #475569;
}
.ik-stack-value {
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #111827;
  font-weight: 700;
}
.ik-stack-muted {
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #6b7280;
}
.grid2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}
.grid3 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 12px;
}
.stat {
  background: #f9fafb;
  border: 1px solid #eef2f7;
  border-radius: 12px;
  padding: 10px;
}
.stat .label {
  font-size: 12px;
  color: #6b7280;
}
.stat .value {
  margin-top: 4px;
  font-size: 18px;
  font-weight: 800;
  color: #111;
}
.grid2.norm-summary-grid {
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 8px;
}

.norm-summary-grid .stat {
  padding: 8px;
}

.norm-summary-grid .stat .label {
  font-size: 11px;
}

.norm-summary-grid .stat .value {
  font-size: 16px;
}
.badge {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 999px;
  background: #eef2ff;
  font-size: 12px;
}

/* --- Temel Bilgiler (polish / readability) --- */
.temel-bilgiler {
  max-width: 100%;
}
.temel-bilgiler .tb-topbar {
  border-radius: 14px;
}
.temel-bilgiler .tb-head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 12px;
  flex-wrap: wrap;
}
.temel-bilgiler .tb-title {
  font-weight: 900;
  letter-spacing: 0.02em;
}
.temel-bilgiler .tb-sub {
  margin-top: 2px;
  font-size: 12px;
  color: #6b7280;
}
.temel-bilgiler .tb-badges {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

/* Each section is its own card (A/B/C/...) */
.temel-bilgiler .tb-card-grid {
  margin-top: 14px;
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  gap: 12px;
}
.temel-bilgiler .tb-card {
  border-radius: 14px;
  padding: 12px;
}
.temel-bilgiler .tb-span-4 {
  grid-column: span 4;
  min-width: 0;
}
.temel-bilgiler .tb-span-6 {
  grid-column: span 6;
  min-width: 0;
}
.temel-bilgiler .tb-span-8 {
  grid-column: span 8;
  min-width: 0;
}
.temel-bilgiler .tb-span-12 {
  grid-column: 1 / -1;
  min-width: 0;
}
.temel-bilgiler .tb-right-stack {
  display: grid;
  grid-auto-rows: min-content;
  gap: 12px;
}

.temel-bilgiler .tb-section-head {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 12px;
  margin-bottom: 10px;
  flex-wrap: wrap;
}
.temel-bilgiler .tb-section-title {
  font-weight: 800;
}
.temel-bilgiler .tb-section-actions {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}
.temel-bilgiler .tb-muted {
  font-size: 12px;
  color: #6b7280;
}
.temel-bilgiler .tb-hint {
  font-size: 12px;
  color: #6b7280;
}

.temel-bilgiler .tb-grid {
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  gap: 10px 12px;
}
.temel-bilgiler .tb-col-3 {
  grid-column: span 3;
  min-width: 0;
}
.temel-bilgiler .tb-col-4 {
  grid-column: span 4;
  min-width: 0;
}
.temel-bilgiler .tb-col-6 {
  grid-column: span 6;
  min-width: 0;
}
.temel-bilgiler .tb-col-8 {
  grid-column: span 8;
  min-width: 0;
}
.temel-bilgiler .tb-col-12 {
  grid-column: 1 / -1;
  min-width: 0;
}

.temel-bilgiler .tb-field label {
  display: block;
  font-size: 12px;
  font-weight: 700;
  color: #374151;
  margin-bottom: 6px;
}
.temel-bilgiler .tb-input {
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}
.temel-bilgiler .tb-num {
  text-align: right;
  font-variant-numeric: tabular-nums;
}

/* Small numeric fields shouldn't look like huge text inputs */
.temel-bilgiler .tb-mini {
  align-self: start;
}
.temel-bilgiler .tb-mini .tb-input {
  width: 140px;
  max-width: 140px;
}
.temel-bilgiler .tb-mini .tb-num {
  text-align: right;
}

.temel-bilgiler .tb-kpis {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 10px;
}
.temel-bilgiler .tb-kpi {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 10px 12px;
}
.temel-bilgiler .tb-kpi .k {
  font-size: 12px;
  color: #6b7280;
  font-weight: 700;
}
.temel-bilgiler .tb-kpi .v {
  margin-top: 4px;
  font-size: 18px;
  font-weight: 900;
  font-variant-numeric: tabular-nums;
}

/* (Legacy) split kept for compatibility; cards now use tb-card-grid */
.temel-bilgiler .tb-split {
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
  gap: 14px;
  margin-top: 14px;
}

.temel-bilgiler .tb-switch {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  font-weight: 700;
  color: #374151;
  user-select: none;
}
.temel-bilgiler .tb-switch input {
  transform: translateY(0.5px);
}

.temel-bilgiler .tb-affix {
  display: flex;
  align-items: center;
  gap: 6px;
  justify-content: center;
}
.temel-bilgiler .tb-affix .tb-input {
  flex: 0 0 auto;
  width: 120px;
  max-width: 120px;
  min-width: 0;
}
.temel-bilgiler .tb-affix-pfx {
  font-weight: 800;
  color: #374151;
}
.temel-bilgiler .tb-affix-sfx {
  font-weight: 800;
  color: #374151;
  margin-left: 2px;
}

.temel-bilgiler .tb-table-wrap {
  overflow: auto;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
}
.temel-bilgiler .tb-fill {
  display: flex;
  flex-direction: column;
  min-height: 0;
}
/* In tall rows (e.g. F) the grid will stretch the card height; let the table area expand to fill that height */
.temel-bilgiler .tb-table-tall {
  flex: 1 1 auto;
  min-height: 0;
  max-height: none;
}
.temel-bilgiler .tb-table {
  border-collapse: separate;
  border-spacing: 0;
}
.temel-bilgiler .tb-table th,
.temel-bilgiler .tb-table td {
  padding: 7px 8px;
}
.temel-bilgiler .tb-table thead th {
  background: #f3f4f6;
}
.temel-bilgiler .tb-table tbody tr:nth-child(even) td {
  background: rgba(17, 24, 39, 0.015);
}
.temel-bilgiler .tb-table tbody tr:hover td {
  background: rgba(17, 24, 39, 0.03);
}
.temel-bilgiler .tb-table td .tb-input {
  min-width: 0;
}
.temel-bilgiler .tb-table th.num,
.temel-bilgiler .tb-table td.num {
  text-align: center;
}
.temel-bilgiler .tb-table td.num .tb-input {
  width: 120px;
  max-width: 120px;
  margin: 0 auto;
}
.temel-bilgiler .tb-table td.num .tb-num {
  text-align: right;
}
.temel-bilgiler .tb-table tbody tr.tb-group-row td {
  background: #f8fafc;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 0.05em;
  color: #334155;
}

@media (max-width: 1100px) {
  .temel-bilgiler .tb-card-grid {
    grid-template-columns: 1fr;
  }
  .temel-bilgiler .tb-span-4,
  .temel-bilgiler .tb-span-6,
  .temel-bilgiler .tb-span-8,
  .temel-bilgiler .tb-span-12 {
    grid-column: 1 / -1;
  }
  .temel-bilgiler .tb-col-3 {
    grid-column: span 6;
  }
  .temel-bilgiler .tb-col-4 {
    grid-column: span 6;
  }
  .temel-bilgiler .tb-col-8 {
    grid-column: 1 / -1;
  }
  .temel-bilgiler .tb-split {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 720px) {
  .temel-bilgiler .tb-col-3,
  .temel-bilgiler .tb-col-4,
  .temel-bilgiler .tb-col-6 {
    grid-column: 1 / -1;
  }
  .temel-bilgiler .tb-kpis {
    grid-template-columns: 1fr;
  }
}
.tabs {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.tab {
  padding: 8px 10px;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  background: #fff;
  cursor: pointer;
  transition:
    background 0.15s ease,
    border-color 0.15s ease,
    color 0.15s ease,
    transform 0.15s ease;
}
.tab:hover {
  border-color: #cbd5e1;
  background: #f3f4f6;
}
.tab.active {
  background: #111827;
  color: #fff;
  border-color: #111827;
}
.tab.active:hover {
  background: #0f172a;
  border-color: #0f172a;
}
.tab.unsaved {
  border-color: #f59e0b;
  background: #fffbeb;
}
.tab.unsaved:hover {
  border-color: #f59e0b;
  background: #fff7ed;
}
.tab.unsaved.active {
  background: #111827;
  color: #fff;
  border-color: #f59e0b;
}
.tab.unsaved.active:hover {
  background: #0f172a;
  border-color: #f59e0b;
}
.tab .tab-unsaved {
  margin-left: 6px;
  font-weight: 900;
  color: #f59e0b;
}
.tab.active .tab-unsaved {
  color: #fef3c7;
}
.tab:active {
  transform: scale(0.98);
}
.school-page .sticky-stack {
