.small {
  font-size: 12px;
  color: #6b7280;
}
hr {
  border: none;
  border-top: 1px solid #e5e7eb;
  margin: 14px 0;
}

/* Tiny helpers */
.pill {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid #e5e7eb;
  background: #fff;
  cursor: pointer;
  user-select: none;
}
.pill.active {
  background: #111827;
  color: #fff;
  border-color: #111827;
}
.pill .dot {
  width: 8px;
  height: 8px;
  border-radius: 999px;
  background: #10b981;
}

.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
  z-index: 1000;
}
.modal {
  width: min(480px, 100%);
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.18);
}
.scenario-wizard-modal {
  width: min(500px, 100%);
  height: min(80vh, 450px);
  max-height: calc(100vh - 32px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.scenario-wizard-header {
  flex: 0 0 auto;
  padding-bottom: 8px;
  border-bottom: 1px solid #e5e7eb;
}
.scenario-wizard-body {
  flex: 1 1 auto;
  min-height: 0;
  overflow-y: auto;
  padding-right: 4px;
  padding-bottom: 12px;
}
.scenario-wizard-footer {
  flex: 0 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding-top: 12px;
  border-top: 1px solid #e5e7eb;
  background: #fff;
}

/* Auth screen */
.auth-shell {
  min-height: 100vh;
  padding: 48px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background:
    radial-gradient(
      800px 420px at 8% 10%,
      rgba(250, 204, 21, 0.18),
      rgba(250, 204, 21, 0)
    ),
    radial-gradient(
      720px 420px at 92% 20%,
      rgba(56, 189, 248, 0.16),
      rgba(56, 189, 248, 0)
    ),
    linear-gradient(
      160deg,
      rgba(253, 246, 233, 0.74) 0%,
      rgba(238, 242, 247, 0.78) 55%,
      rgba(248, 250, 252, 0.82) 100%
    ),
    var(--auth-bg, none);
  background-repeat: no-repeat;
  background-size: auto, auto, auto, cover;
  background-position:
    center,
    center,
    center,
    0% 75%;
  color: #0f172a;
}
.auth-shell * {
  box-sizing: border-box;
}
.auth-hero,
.auth-card {
  animation: auth-fade-up 0.85s ease-out both;
}
.auth-card {
  animation-delay: 0.08s;
}
.auth-grid {
  width: min(1120px, 100%);
  display: grid;
  grid-template-columns: minmax(0, 1.1fr) minmax(0, 0.9fr);
  gap: 32px;
  align-items: stretch;
}
.auth-hero {
  padding: 24px 8px 24px 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.auth-badge {
  align-self: flex-start;
  padding: 6px 12px;
  border-radius: 999px;
  font-family: "Manrope", "Segoe UI", sans-serif;
  font-size: 12px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #1e293b;
  background: rgba(15, 23, 42, 0.08);
}
.auth-title {
  margin: 0;
  font-family: "Fraunces", "Times New Roman", serif;
  font-size: clamp(32px, 4vw, 54px);
  line-height: 1.05;
  color: #111827;
}
.auth-subtitle {
  margin: 0;
  font-family: "Manrope", "Segoe UI", sans-serif;
  font-size: 16px;
  color: #475569;
  max-width: 520px;
}
.auth-highlights {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 12px;
}
.auth-metric {
  font-family: "Fraunces", "Times New Roman", serif;
  font-size: 28px;
  color: #0f172a;
}
.auth-caption {
  font-family: "Manrope", "Segoe UI", sans-serif;
  font-size: 12px;
  color: #64748b;
}
.auth-card {
  background: rgba(255, 255, 255, 0.96);
  border: 1px solid rgba(226, 232, 240, 0.9);
  border-radius: 18px;
  padding: 28px;
  box-shadow: 0 18px 36px rgba(15, 23, 42, 0.12);
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.auth-card-head {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.auth-card-title {
  font-family: "Fraunces", "Times New Roman", serif;
  font-size: 28px;
  color: #0f172a;
}
.auth-card-sub {
  font-family: "Manrope", "Segoe UI", sans-serif;
  font-size: 13px;
  color: #64748b;
}
.auth-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.auth-options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  font-family: "Manrope", "Segoe UI", sans-serif;
  font-size: 13px;
  color: #475569;
}
.auth-option {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  user-select: none;
}
.auth-option input {
  accent-color: #0f172a;
}
.auth-label {
  font-family: "Manrope", "Segoe UI", sans-serif;
  font-size: 12px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #475569;
}
.auth-input {
  width: 100%;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 10px 12px;
  font-size: 15px;
  font-family: "Manrope", "Segoe UI", sans-serif;
  background: #fff;
  transition:
    border-color 0.15s ease,
    box-shadow 0.15s ease;
}
.auth-input-wrap {
  position: relative;
  width: 100%;
}
.auth-input-wrap .auth-input {
  padding-right: 44px;
}
.auth-input-eye {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  border: 0;
  border-radius: 10px;
  background: transparent;
  color: #64748b;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition:
    color 0.15s ease,
    background 0.15s ease;
}
.auth-input-eye:hover {
  color: #0f172a;
  background: rgba(15, 23, 42, 0.08);
}
.auth-input-eye svg {
  width: 18px;
  height: 18px;
}
.auth-input:focus {
  outline: none;
  border-color: #0f172a;
  box-shadow: 0 0 0 3px rgba(15, 23, 42, 0.12);
}
.auth-error {
  border-radius: 12px;
  border: 1px solid #fecaca;
  background: #fff1f2;
  padding: 10px 12px;
  font-size: 13px;
  color: #991b1b;
}
.auth-help {
  font-size: 12px;
  color: #64748b;
  font-family: "Manrope", "Segoe UI", sans-serif;
}
@keyframes auth-fade-up {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (prefers-reduced-motion: reduce) {
  .auth-hero,
  .auth-card {
    animation: none;
  }
}
@media (max-width: 900px) {
  .auth-shell {
    padding: 24px 16px;
  }
  .auth-grid {
    grid-template-columns: 1fr;
  }
  .auth-hero {
    padding-right: 0;
  }
  .auth-highlights {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 640px) {
  .auth-highlights {
    grid-template-columns: 1fr;
  }
}

/* ===== SchoolPage unified topbar + modern tabs ===== */

.school-page .school-nav {
  background: rgba(255, 255, 255, 0.78);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(226, 232, 240, 0.9);
  border-radius: 16px;
  padding: 10px;
  box-shadow: 0 10px 26px rgba(15, 23, 42, 0.08);
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Top bar */
.school-page .school-topbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.school-page .school-topbar-left {
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 0;
}

.school-page .school-back-btn {
  height: 36px;
  width: 36px;
  border-radius: 999px;
  border: 1px solid rgba(226, 232, 240, 0.9);
  background: rgba(255, 255, 255, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  box-shadow: 0 6px 16px rgba(15, 23, 42, 0.06);
  transition:
    transform 120ms ease,
    background 160ms ease;
}
.school-page .school-back-btn:hover {
  transform: translateY(-1px);
  background: #fff;
}
.school-page .school-back-btn:active {
  transform: translateY(0px) scale(0.98);
}

.school-page .school-topbar-text {
  min-width: 0;
}
.school-page .school-topbar-title {
  font-weight: 900;
  font-size: 16px;
  line-height: 1.1;
  color: #0f172a;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 520px;
}
.school-page .school-topbar-sub {
  margin-top: 4px;
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  align-items: center;
}

.school-page .school-pill {
  display: inline-flex;
  align-items: center;
  padding: 4px 10px;
  border-radius: 999px;
  font-size: 12px;
  border: 1px solid rgba(226, 232, 240, 0.9);
  background: rgba(15, 23, 42, 0.04);
  color: #334155;
}
.school-page .school-pill.is-placeholder {
  visibility: hidden;
}
.school-page .school-pill.is-muted {
  color: #64748b;
  background: rgba(148, 163, 184, 0.12);
}
.school-page .school-pill.is-warn {
  border-color: rgba(245, 158, 11, 0.5);
  background: #fffbeb;
  color: #92400e;
}
.school-page .school-pill.is-ok {
  border-color: rgba(16, 185, 129, 0.45);
  background: rgba(16, 185, 129, 0.1);
  color: #065f46;
}
.school-page .school-pill.is-bad {
  border-color: rgba(220, 38, 38, 0.45);
  background: rgba(220, 38, 38, 0.1);
  color: #991b1b;
}

.school-page .school-topbar-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 10px;
}
.school-page .report-export {
  background: #fff;
}
.status-badge {
  display: inline-flex;
  align-items: center;
  padding: 3px 8px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 600;
  border: 1px solid rgba(226, 232, 240, 0.9);
  background: rgba(15, 23, 42, 0.04);
  color: #334155;
}
.status-badge.is-muted {
  background: rgba(148, 163, 184, 0.12);
  color: #64748b;
}
.status-badge.is-warn {
  border-color: rgba(245, 158, 11, 0.5);
  background: #fffbeb;
  color: #92400e;
}
.status-badge.is-ok {
  border-color: rgba(16, 185, 129, 0.45);
  background: rgba(16, 185, 129, 0.1);
  color: #065f46;
}
.status-badge.is-bad {
  border-color: rgba(220, 38, 38, 0.45);
  background: rgba(220, 38, 38, 0.1);
  color: #991b1b;
}
.status-badge.is-draft,
.status-badge.is-unknown {
  border-color: rgba(148, 163, 184, 0.35);
  background: rgba(148, 163, 184, 0.12);
  color: #64748b;
}
.status-badge.is-submitted {
  border-color: rgba(245, 158, 11, 0.5);
  background: #fffbeb;
  color: #92400e;
}
.status-badge.is-revision {
  border-color: rgba(220, 38, 38, 0.45);
  background: rgba(220, 38, 38, 0.1);
  color: #991b1b;
}
.status-badge.is-approved {
  border-color: rgba(16, 185, 129, 0.45);
  background: rgba(16, 185, 129, 0.1);
  color: #065f46;
}

.school-page .school-actions {
  display: flex;
  gap: 8px;
  align-items: center;
  flex-wrap: wrap;
}

/* Sticky footer (actions + status pills) */
.school-page .school-sticky-footer {
  position: fixed;
  bottom: 0;
  left: 250px;
  right: 0;
  z-index: 140;
  padding: 0;
  pointer-events: none;
}
.app-shell.is-collapsed .school-page .school-sticky-footer {
  left: 88px;
}
.school-page .school-sticky-footer-inner {
  min-height: 64px;
  height: 64px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: minmax(0, 1fr) auto minmax(0, 1fr);
  align-items: center;
  column-gap: 12px;
  background: rgba(255, 255, 255, 0.9);
  border-top: 1px solid rgba(226, 232, 240, 0.9);
  border-radius: 0;
  padding: 10px 50px;
  box-shadow: 0 -8px 20px rgba(15, 23, 42, 0.08);
  backdrop-filter: blur(10px);
  font-family: "Poppins", "Manrope", "Segoe UI", sans-serif;
  pointer-events: auto;
}

/* Footer progress bar and modules */
.school-page .school-footer-progress {
  display: flex;
  gap: 6px;
  height: 10px;
  width: 100%;
  margin-bottom: 4px;
}
.school-page .school-footer-progress-seg {
  flex: 1 1 0;
  background: var(--neutral-200, #f3f4f6);
  border-radius: 999px;
  overflow: hidden;
}
.school-page .school-footer-progress-fill {
  height: 100%;
  width: 0%;
  background: var(--accent-500, #2563eb);
}

.school-page .school-footer-left {
  /* Keep the status/metadata section sized to its content and prevent it from stretching. */
  display: flex;
  flex-direction: column;
  gap: 4px;
  justify-content: center;
  grid-column: 1;
  justify-self: start;
  max-width: clamp(240px, 30vw, 360px);
  width: 100%;
  min-width: 0;
  overflow: hidden;
  position: relative;
  height: 100%;
  padding-bottom: 14px;
}
.school-page .school-footer-pills {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 0 0 auto;
  min-width: 0;
  overflow: hidden;
  flex-wrap: nowrap;
  min-height: 24px;
}

/* Module progress bar in sticky footer */
.school-page .module-progress {
  display: flex;
  align-items: center;
  gap: 4px;
  /* Occupy available width and allow the bar to be wider than the previous 240px cap. */
  width: 100%;
  /* Allow the progress bar to grow wider so that modules remain legible. */
  max-width: 480px;
  margin: 0 8px;
}

/* When the progress bar is wrapped in .progress-wrapper (sticky footer),
   expand it to use the available width and remove the right margin limit. */
.school-page .progress-wrapper .module-progress {
  margin-right: 0;
  max-width: none;
  width: 100%;
}

/* Container for the module progress bar, allowing it to grow while keeping a reasonable
   minimum and maximum width. This ensures the progress bar sits in the middle of the
   footer and uses the available horizontal space. */
.school-page .progress-wrapper {
  display: flex;
  flex: 1 1 auto;
  min-width: 200px;
  max-width: 420px;
  margin-right: 8px;
}

/* Container for the action buttons within the sticky footer.  Keeps buttons together
   and prevents them from stretching. */
.school-page .button-wrapper {
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  gap: 8px;
}
.school-page .module-seg {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Allow each module segment to shrink when space is constrained but
     still maintain readability. */
  min-width: 32px;
  flex: 1 1 0;
  border: 0;
  padding: 0;
  background: transparent;
}
.school-page .module-seg.is-clickable {
  cursor: pointer;
}
.school-page .module-seg.is-clickable .module-bar {
  box-shadow: 0 0 0 1px rgba(15, 23, 42, 0.12);
}
.school-page .module-seg.is-clickable:hover .module-bar {
  box-shadow: 0 0 0 2px rgba(16, 185, 129, 0.45);
}
.school-page .module-seg.is-clickable:focus-visible {
  outline: 2px solid rgba(16, 185, 129, 0.55);
  outline-offset: 2px;
  border-radius: 8px;
}
.school-page .module-title {
  font-size: 13px;
  color: var(--neutral-700);
  line-height: 1;
  margin-bottom: 2px;
  text-align: center;
  white-space: nowrap;
  width: 100%;
}
.school-page .module-bar {
  position: relative;
  width: 100%;
  height: 14px;
  background: var(--neutral-200);
  border-radius: 999px;
  overflow: hidden;
}
.school-page .module-fill {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--neutral-50);
  font-size: 13px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.2px;
  padding-left: 1px;
  padding-right: 1px;
  white-space: nowrap;
  transition: width 0.3s ease;
  background: #9ca3af;
}
.school-page .module-fill.is-draft {
  background: #9ca3af;
}
.school-page .module-fill.is-review {
  background: #f59e0b;
}
.school-page .module-fill.is-forwarded {
  background: #0ea5a4;
}
.school-page .module-fill.is-revision {
  background: #dc2626;
}
.school-page .module-fill.is-approved {
  background: #2563eb;
}
.school-page .module-fill.is-final {
  background: #16a34a;
}
.school-page .module-fill.is-done {
  background: var(--success-600, #15803d);
}
.school-page .module-percent {
  pointer-events: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  color: #fff;
  font-variant-numeric: tabular-nums;
  -webkit-font-smoothing: antialiased;
  text-rendering: geometricPrecision;
  text-shadow: 0 1px 2px rgba(15, 23, 42, 0.35);
}

.school-page .school-footer-scenario {
  flex: 1 1 auto;
  min-width: 0;
  max-width: 560px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.school-page .school-footer-meta {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: absolute;
  left: 0;
  bottom: 0;
}
.school-page .school-footer-meta.is-placeholder {
  visibility: hidden;
}
.school-page .school-footer-actions {
  /* Allow the footer actions area to flex and accommodate a wider progress bar.
     We override the default max-width to let the actions area consume more space. */
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1 1 auto;
  max-width: none;
  overflow-x: auto;
  flex-wrap: nowrap;
  scrollbar-width: thin;
}

/* Split the footer actions into a middle progress bar and right button area.
   The middle section expands to use available space, while the right section
   remains sized to its contents. */
.school-page .school-footer-middle {
  grid-column: 2;
  justify-self: center;
  display: flex;
  justify-content: center;
  /* Provide reasonable minimum and maximum widths so the bar can grow but not overwhelm other elements */
  width: clamp(220px, 40vw, 480px);
  min-width: 200px;
  max-width: 480px;
}
.school-page .school-footer-right {
  grid-column: 3;
  justify-self: end;
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 0;
  /* Allow the export menu to escape the footer without being clipped. */
  overflow: visible;
}
.school-page .school-sticky-footer .action-menu-panel {
  top: auto;
  bottom: calc(100% + 6px);
}

/* Sticky footer for permissions pages */
.permissions-page .permissions-page-content {
  padding-bottom: 96px;
}
.permissions-page .permissions-sticky-footer {
  position: fixed;
  bottom: 0;
  left: 250px;
  right: 0;
  z-index: 140;
  padding: 0;
  pointer-events: none;
}
.app-shell.is-collapsed .permissions-page .permissions-sticky-footer {
  left: 88px;
}
.permissions-page .permissions-sticky-footer-inner {
  height: 64px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  background: rgba(255, 255, 255, 0.9);
  border-top: 1px solid rgba(226, 232, 240, 0.9);
  border-radius: 0;
  padding: 10px 50px;
  box-shadow: 0 -8px 20px rgba(15, 23, 42, 0.08);
  backdrop-filter: blur(10px);
  font-family: "Poppins", "Manrope", "Segoe UI", sans-serif;
  pointer-events: auto;
}
.permissions-page .permissions-footer-pills {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1 1 auto;
  min-width: 0;
  overflow-x: auto;
  flex-wrap: nowrap;
}
.permissions-page .permissions-footer-actions {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 0 0 auto;
}


/* Action menu (export dropdown) */
.action-menu {
  position: relative;
  display: inline-block;
}
.action-menu-panel {
  position: absolute;
  right: 0;
  top: calc(100% + 6px);
  min-width: 180px;
  background: #fff;
  border: 1px solid rgba(226, 232, 240, 0.9);
  border-radius: 10px;
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.12);
  padding: 6px;
  z-index: 50;
}
.action-menu-item {
  width: 100%;
  text-align: left;
  padding: 10px 10px;
  border-radius: 8px;
  border: 0;
  background: transparent;
  cursor: pointer;
  font-weight: 600;
  color: #0f172a;
}
.action-menu-item:hover {
  background: rgba(15, 23, 42, 0.06);
}
.action-menu-item:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Scenario row actions */
.scenario-action-btn {
  padding: 6px 10px;
  font-size: 12px;
  border-radius: 999px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
}
.scenario-action-btn svg {
  font-size: 13px;
}
.scenario-action-btn:disabled {
  opacity: 0.6;
}
.scenario-table {
  table-layout: fixed;
}
.scenario-table th {
  user-select: none;
}
.scenario-table th:first-child,
.scenario-table td:first-child {
  width: 32px;
  padding-right: 4px;
}
.scenario-table th:nth-child(2),
.scenario-table td:nth-child(2) {
  padding-left: 4px;
}
.scenario-name {
  display: block;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.scenario-row-actions {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  flex-wrap: nowrap;
  white-space: nowrap;
}
.scenario-row.is-selected {
  background: rgba(59, 130, 246, 0.08);
}
.scenario-toolbar {
  display: flex;
  gap: 6px;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: center;
}
.scenario-toolbar .btn {
  min-width: 120px;
  height: 32px;
  padding: 0 10px;
  border-radius: 10px;
  font-size: 12px;
}

/* Review queue filters and module links */
.review-queue-tabs {
  --rq-tab-count: 7;
  --rq-active-index: 0;
  margin: 6px 0 16px;
  padding: 0;
  border-radius: 16px;
  position: relative;
  overflow: hidden;
  background: #3b82f6;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
}
.rq-tab-input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}
.rq-tab-nav {
  background: #ffffff;
  width: 100%;
  height: 5rem;
  position: relative;
  display: flex;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
  min-width: calc(var(--rq-tab-count) * 140px);
  overflow-x: auto;
  scrollbar-width: thin;
}
.rq-tab-nav::-webkit-scrollbar {
  height: 6px;
}
