}

.rq-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 80;
}

.rq-modal-wrap {
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  padding: 12px;
  z-index: 90;
}

.rq-close-btn {
  position: fixed;
  top: 12px;
  right: 12px;
  width: 38px;
  height: 38px;
  border-radius: 999px;
  border: 1px solid rgba(15, 23, 42, 0.12);
  background: #ffffff;
  display: grid;
  place-items: center;
  box-shadow: 0 18px 26px rgba(15, 23, 42, 0.14);
  cursor: pointer;
  z-index: 95;
}

.rq-close-icon {
  width: 18px;
  height: 18px;
  color: #0f172a;
}

.rq-modal {
  width: 100%;
  max-width: 920px;
  max-height: 90vh;
  overflow: hidden;
  padding: 0;
  display: flex;
  flex-direction: column;
  border-radius: 22px;
}

.rq-modal-head {
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 14px 16px;
  border-bottom: 1px solid #e5e7eb;
  background: linear-gradient(180deg, rgba(248, 250, 252, 1), rgba(255, 255, 255, 1));
}

.rq-modal-head-text {
  display: flex;
  flex-direction: column;
}

.rq-modal-top-actions {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.rq-modal-body {
  padding: 14px 16px 18px;
  overflow: auto;
}

/* Give the table a little breathing room in the modal */
.rq-modal .table {
  margin-top: 10px;
}

/* Better mobile layout */
@media (max-width: 520px) {
  .rq-modal {
    max-height: 92vh;
    border-radius: 18px;
  }
  .rq-thumb--lg {
    width: 62px;
    height: 62px;
    border-radius: 20px;
  }
  .rq-title--lg {
    font-size: 16px;
  }
}
